select {
	background:white;
	border: 1px solid #F0F3FB;
	border-radius: 30px;
  width: 170px;
  height: 30px;
  font-size: 0.80em;
  font-family: Poppins,sans-serif;
  color:black;
  padding-left:30px;

	/* Here's the code we need */
	-webkit-appearance: none;
	-moz-appearance: none;
	-ms-appearance: none;
	 -o-appearance: none;
		appearance: none;
}

.select-wrapper {
	position: relative;	
}

.select-wrapper::after {
	font-family: FontAwesome;
  	content: '\f107'; 
  	font-size: 28px;
  	position: absolute;
  	top: 0px;
    right:10px;
    color:black;
  	pointer-events: none;
}

select::-ms-expand {
  display: none;
}

